 
.custom-button {
  position: relative;
  display: block;
  background-color: transparent;
  border: none;
  cursor: pointer;

  background-image: url('button_blue_n.svg');
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
} 

.custom-button-header {
  width: 12vh;
  height: 100%;
}

.custom-button:hover {
background-image: url('button_blue_h.svg');
}

.background-svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

.button-text {
    position: relative;
    z-index: 1;
    color: white;
    font-size: 2vh;
  }

