.header {
    background-color: #282c34;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    color: rgb(255, 255, 255);
    height: 5vh;
    width: 100%;    
    font-size: 3vh;
} 

.side-left {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
    width: calc((100% - 10vh)/2);
}

.title-version {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  font-size: 3vh;
}

.title {
  line-height: 90%;
}

.version {
  font-size: calc(1.5vmin);
}

.side-right {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    width: calc((100% - 10vh)/2);
    gap: 2vh;
    height: 100%;
}

.logo {
    color: #61dafb;
    height: 100%;
    width: 10vh;
  }

.logo-link {
    display: block;
    height: 100%;
    width: 100%;
  }

.logo-image {
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  
@media (prefers-reduced-motion: no-preference) {
  .logo-image {
      animation: App-logo-spin infinite 20s linear;
    }
}

@keyframes App-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
} 
 